@-webkit-keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@-moz-keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@keyframes zoom-out-enter {
  0% {
    opacity: 0;
    transform: scale(1.3); }
  100% {
    opacity: 1;
    transform: scale(1); } }

@-webkit-keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }

@-moz-keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }

@keyframes zoom-out-leave {
  0% {
    opacity: 1;
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1.3); } }
